import * as React from 'react';
import { Plugin } from '@ali/4ever-cangjie';
import { Placeholder, InlinePlaceholder } from '@ali/4ever-bamboo';
import { moFactory } from '@ali/4ever-plugin-placeholder';

export default function createPlaceholderPlugin(config): Plugin {
  const placeHolderConfig = config.placeholder || {};
  const { renderText } = placeHolderConfig;
  return {
    ...moFactory({}),
    renderNode: {
      ph: (props) => {
        return (
          <Placeholder {...props} renderText={renderText} />
        );
      },
      iph: (props) => {
        return (
          <InlinePlaceholder
            {...props}
            renderText={renderText}
          />
        );
      },
    },
  };
}
